.floatwnd {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 99;
    text-align: left;
    outline: none;
    box-shadow: 5px  5px 40px 5px;
}

.floatwnd.active {
    display: inline-block;
}

#note-contextmenu {
    background-color: rgba(0, 0, 0, 0.829);
    border: solid 2px rgb(255, 0, 0);
    width: 250px;
    /* height: 100px; */
}

#note-contextmenu>div {
    display: inline-block;
    width: 100%;
    height: 20px;
    /* margin-bottom: 2px; */
    /* background-color: rgba(127, 255, 212, 0.582); */
}

#note-contextmenu>div:hover {
    background-color: rgba(251, 255, 0, 0.185);
}

#note-contextmenu>div.disable {
    opacity: 0.5;
    pointer-events: none;
}

#note-contextmenu>div>span {
    display: inline-block;
    margin-top: 1px;
    font-weight: 600;
    font-size: 14px;
}

#note-contextmenu>div>.order {
    margin-left: 5px;
    color: red;
}

#note-contextmenu>div>.call {
    margin-left: 5px;
    color: rgb(255, 255, 255);
}

#note-contextmenu>div>.key {
    margin-left: 2px;
    color: rgba(255, 255, 255, 0.473);
}
