#scorecont-sourcetab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 127, 202, 0.664); */
    text-align: left;
    background-color: blanchedalmond;
}

#scorecont-sourcetab-root>* {
    display:  inline-block;
    width: calc(100% - 8px);
    margin: 4px 0 0 4px;
}
#scorecont-sourcetab-root>textarea {
    background-color: rgb(245, 245, 245);
    font-size: 16px;
    color: rgba(0, 0, 0, 0.849);
    height: calc(100% - 38px);
    border: solid 1px rgb(177, 177, 177);
    border-radius: 4px;
    resize: none;
}
#scorecont-sourcetab-root>button {
    height: 28px;
    background-color: rgb(180, 180, 180);
    border: solid 1px rgb(85, 85, 85);
    border-radius: 3px;
    font-size: 20px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.685);
    text-align: center;
}
#scorecont-sourcetab-root>button:hover {
    background-color: rgb(193, 209, 184);
}