#score-footer {
    margin-top: 2px;
    display: block;
    height: 147px;
    width: 100%;
    background-color: #fdfcf4;
    text-align: left;
}

#score-footer>.tab-list>.tab-menu {
    background-color: rgba(131, 128, 114, 0.603);
}

#score-footer>.tab-list>.tab-menu.tab-active {
    background-color: rgba(199, 195, 175, 0.671);
}

#score-footer>.cont-list {
    background-color: rgba(199, 195, 175, 0.671);
}

.footer-child {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgb(235, 233, 229);
}

#footer-timing {}

#footer-timing>.scroll {
    overflow-x: hidden;
    text-align: left;
    white-space: nowrap;
}

#footer-timing>.scroll>.blocks {
    display: block;
    /* position: relative; */
    height: 46px;
    /* width: 2200px; */
    background-color: rgb(221, 221, 221);
}

#footer-timing>.scroll>.blocks>div {
    display: inline-block;
    height: calc(100% - 6px);
    margin-top: 3px;
    margin-left: 1px;
    background-color: rgb(255, 255, 255);
    /* background: linear-gradient(to right,white, rgb(167, 167, 167), white); */
    /* border: solid 1px rgba(194, 0, 0, 0.418); */
    text-align: center;
    opacity: 0.2;
    vertical-align: top;
}
#footer-timing>.scroll>.blocks>.active {
    /* background-color: rgb(255, 250, 181); */
    opacity: 1;
    border: solid 1px rgb(179, 0, 0);
    /* box-shadow: 0 0 10px 10px #0000002a; */
    /* height: calc(100% - 12px);
    margin-top: 6px; */
}
#footer-timing>.scroll>.blocks>.near {
    border: solid 1px rgb(0, 0, 0);
    background-color: rgb(213, 238, 253);
    opacity: 0.3;
}

#footer-timing>.scroll>.blocks>div>div{
    display: block;
    width: 100%;
    height: 50%;
}

#footer-timing>.scroll>.blocks>div>.sus {
    /* background-color: rgba(137, 43, 226, 0.11); */
}

#footer-timing>.scroll>.blocks>div>.sus>span {
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    color: rgb(144, 0, 0);
    /* margin: 1px 0 0 0; */
}
#footer-timing>.scroll>.blocks>div>.adjust {
    position: relative;
}
#footer-timing>.scroll>.blocks>div>.adjust>span {
    position: absolute;
    font-size: 16px;
    font-weight: 600;
}

#footer-timing>.scroll>.blocks>div>.adjust>span:nth-child(1) {
    left: 5px;
}
#footer-timing>.scroll>.blocks>div>.adjust>span:nth-child(2) {
    right: 5px;
}
#footer-timing>.scroll>.blocks>div>.adjust>span.aug{
    color: rgb(36, 0, 194);
}
#footer-timing>.scroll>.blocks>div>.adjust>span.dim{
    color: rgb(194, 0, 0);
}

#footer-timing>.scroll>.measure {
    display: inline-block;
    position: relative;
    height: 50px;
    /* background-color: rgb(253, 250, 236); */
    border-top: solid 2px rgb(128, 128, 128);
    background: linear-gradient(to bottom, rgb(255, 255, 255), rgba(0, 0, 0, 0.349));
}

#footer-timing>.scroll>.measure>div {
    display: inline-block;
    position: absolute;
    top: 0;
    width: 1px;
    background-color: rgb(128, 128, 128);
}
#footer-timing>.scroll>.measure>div>.bar {
    display: inline-block;
    position: absolute;
    top: 0;
    font-size: 18px;
    font-weight: 600;
    color: rgb(224, 4, 4);
    /* background: rgba(255, 255, 255, 0.527); */
    width: 30px;
    text-align: center;
}

#footer-timing>.scroll>.measure>.blank {
    width: 100px;
}
#footer-timing>.scroll>.measure>.mem16 {
    height: 8px;
    /* background-color: rgb(255, 255, 255); */
}

#footer-timing>.scroll>.measure>.mem8 {
    height: 12px;
    /* background-color: rgb(33, 36, 255); */
}

#footer-timing>.scroll>.measure>.mem4 {
    height: 18px;
    /* background-color: rgb(255, 44, 44); */
}
#footer-timing>.scroll>.measure>.mem-bar {
    height: 22px;
    /* background-color: rgb(0, 0, 0); */
    width: 2px;
}