#backing-palette {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 594px;
    /* height: 400px; */
    left: 50px;
    top: 50px
}

#backing-palette>div {
    display: inline-block;
    width: calc(100% - 4px);
    margin: 2px 0 0 2px;
    background-color: rgba(255, 255, 255, 0.459);
}

.base-info {}

.base-info>.record {
    display: inline-block;
    width: calc(100% - 4px);
    height: 20px;
    margin: 1px 0 0 2px;
    background-color: rgba(255, 255, 255, 0.473);
}

.base-info>.record>.title {
    display: inline-block;
    width: 120px;
    height: calc(100% - 2px);
    margin: 1px;
    background-color: rgba(255, 0, 0, 0.336);
}

.base-info>.record>.value {
    display: inline-block;
    width: calc(100% - 124px);
    height: calc(100% - 2px);
    margin: 1px;
    /* background-color: rgba(255, 0, 0, 0.336); */
}

.base-info>.record>*>span {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    margin: 0 0 0 4px;
}

.base-info>.record>.title>span {
    color: rgb(136, 0, 0);
}

.base-info>.record>.value>span {
    color: rgb(0, 0, 0);
}

.struct-table {}

.struct-table>.record {
    display: inline-block;
    width: calc(100% - 4px);
    height: 20px;
    margin: 1px 0 0 2px;
    background-color: rgba(255, 255, 0, 0.473);
    /* transition: background-color 250ms; */
}

.struct-table>.record>.label {
    display: inline-block;
    width: 123px;
    height: calc(100% - 2px);
    margin: 1px;
    background-color: rgba(255, 255, 255, 0.301);
    vertical-align: top;
}

.struct-table>.record>.label>span {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    margin: 0 0 0 4px;
    color: green;
}

.struct-table>.record>.col {
    display: inline-block;
    width: 55px;
    height: calc(100% - 2px);
    margin: 1px;
    background-color: rgba(61, 43, 226, 0.301);
    text-align: center;
    transition: background-color 100ms;
}

.struct-table>.record>.col:hover {
    background-color: rgba(204, 199, 253, 0.541);
}

.struct-table>.record>.col.active {
    background-color: rgba(254, 255, 173, 0.822);
}

.struct-table>.record>.col.active.over {
    background-color: rgba(254, 255, 173, 0.459);
}

.struct-table>.record>.col>span {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    margin: 0 0 0 4px;
    color: rgb(55, 0, 184);
}

.struct-table>.record>.col.active.over>span {
    color: rgb(212, 0, 0);
}

.backing-table {
    /* height: 250px; */
}

.backing-table>div {
    display: inline-block;
    vertical-align: top;
}

.backing-table>.switch {
    height: 25px;
    background-color: rgba(0, 16, 240, 0.253);
    width: calc(100% - 4px);
    margin: 2px 2px 0 2px;
}

.backing-table>.switch>select {
    display: inline-block;
    margin: 2px 4px 2px 2px;
    width: 40px;
    /* padding: 0 10px; */
    height: calc(100% - 4px);
    background-color: rgba(255, 251, 248, 0.041);
    border: solid 1px rgba(255, 251, 248, 0.658);
    border-radius: 2px;
    text-align: left;
    box-sizing: border-box;
    color: rgb(255, 255, 0);
    font-size: 14px;
    font-weight: 600;
    padding: 0 0 0 4px;
}

.backing-table>.switch>select>option {
    font-size: 14px;
    font-weight: 600;
    color: white;
    background-color: rgb(111, 111, 143);
}

.backing-table>.switch>select>option.del {
    color: rgb(247, 0, 0);
}

.backing-table>.switch>button {
    display: inline-block;
    margin: 2px 0 2px 2px;
    min-width: 40px;
    padding: 0 10px;
    height: calc(100% - 4px);
    background-color: rgba(255, 251, 248, 0.137);
    border: solid 1px rgba(255, 251, 248, 0.658);
    border-radius: 1px;
    text-align: center;
}

.backing-table>.switch>button:hover {
    background-color: rgba(185, 229, 255, 0.397);
}

.backing-table>.switch>button.active {
    background-color: rgba(248, 251, 48, 0.397);
}

.backing-table>.switch>button.active:hover {
    background-color: rgba(253, 255, 159, 0.445);
}

.backing-table>.switch>button>span {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    color: rgba(255, 251, 248, 0.658);
    margin-top: -2px;
}

.backing-table>.fixed {
    display: inline-block;
    width: 78px;
    background-color: rgb(0, 16, 240);
    margin: 2px 2px 2px 2px;
}

.backing-table>.main {
    display: inline-block;
    width: calc(100% - 84px);
    background-color: rgb(0, 16, 240);
    margin: 2px 0 2px 0;
    overflow: auto;
}

.backing-table>div>div {
    display: inline-block;
    width: calc(100% - 4px);
    margin: 2px 2px 0 2px;
}

.backing-table>div>.split {
    height: 22px;
    background-color: rgba(255, 105, 180, 0.37);
    white-space: nowrap;
}

.backing-table>.main>.split {
    min-width: calc(100% - 4px);
    width: initial;
}

.backing-table>div>.split>div {
    display: inline-block;
    height: calc(100% - 2px);
    margin: 1px 0 1px 1px;
    border: solid 1px rgba(255, 255, 255, 0.589);
    /* background-color: rgba(0, 0, 0, 0.37); */
    text-align: center;
    transition: width 100ms;
}

.backing-table>div>.split>div:hover {
    background-color: rgba(114, 234, 255, 0.212);
}

.backing-table>div>.split>div.active {
    background-color: rgba(255, 255, 255, 0.274);
}

.backing-table>div>.split>div.active:hover {
    background-color: rgba(184, 241, 255, 0.37);
}

.backing-table>div>.split>div>span {
    font-size: 15px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.589);
}

.len4 {
    width: 123px;
}

.len4.half {
    width: 185px;
}

.len8 {
    width: 61px;
}

.len8.half {
    width: 92px;
}

.len16 {
    width: 30px;
}

.len1 {
    width: 495px;
}

.len2 {
    width: 247px;
}

.len2.half {
    width: 371px;
}

.backing-table>div>.measure {
    height: 20px;
    /* background-color: rgba(255, 105, 180, 0.37); */
}

.backing-table>.main>.measure {
    background: linear-gradient(rgba(0, 0, 0, 0.74), rgba(0, 0, 0, 0));
    min-width: calc(100% - 4px);
    width: initial;
    white-space: nowrap;
    position: relative;
}

.backing-table>.main>.measure>div {
    display: inline-block;
    vertical-align: top;
    margin-right: 30px;
    width: 1px;
}

.backing-table>.main>.measure>.mem16 {
    height: 6px;
    background-color: rgba(156, 224, 255, 0.685);
}

.backing-table>.main>.measure>.mem8 {
    height: 10px;
    background-color: rgba(9, 34, 255, 0.76);
}

.backing-table>.main>.measure>.mem4 {
    height: 18px;
    background-color: rgba(233, 0, 0, 0.678);
}

.backing-table>.main>.measure>.limit {
    height: 100%;
    background-color: rgba(233, 229, 0, 0.678);
    width: 2px;
    margin-right: 0;
}

.backing-table>.main>.measure>.safe {
    position: absolute;
    z-index: 10;
    left: 0;
    top: 0;
    height: 180px;
    pointer-events: none;
    background-color: rgba(255, 255, 255, 0.685);
}

.backing-table>.main>.measure>.over {
    position: absolute;
    z-index: 10;
    left: 0;
    top: 0;
    height: 180px;
    pointer-events: none;
    animation: length-over 2s linear infinite;
}

@keyframes length-over {
    0%, 100% {
        background-color: rgba(255, 0, 0, 0.411);
    }
    50% {
        background-color: rgba(104, 0, 0, 0.411);
    }
}

/* レイヤー */

.backing-table>.fixed>.layer {
    height: 44px;
    background-color: rgba(0, 0, 0, 0.445);
}

.backing-table>.fixed>.layer>button {
    display: inline-block;
    margin: 1px 0 0 2px;
    width: calc(100% - 4px);
    height: 20px;
    background-color: rgba(255, 255, 255, 0.233);
    border: solid 1px rgba(255, 255, 255, 0.658);
    border-radius: 2px;
    text-align: center;
}

.backing-table>.fixed>.layer>button:hover {
    background-color: rgba(185, 229, 255, 0.397);
}

.backing-table>.fixed>.layer>button.active {
    background-color: rgba(48, 244, 251, 0.562);
}

.backing-table>.fixed>.layer>button.active:hover {
    background-color: rgba(255, 255, 255, 0.445);
}

.backing-table>.fixed>.layer>button>span {
    display: inline-block;
    font-size: 12px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.658);
    margin-top: -1px;
}

.backing-table>div>.matrix {
    height: 158px;
    background-color: rgba(255, 105, 180, 0.37);
}

.backing-table>.fixed>.matrix>div {
    width: 100%;
    height: 12px;
    margin-top: 1px;
}

.backing-table>.fixed>.matrix>div>div {
    display: inline-block;
    height: 100%;
    margin: 0 1px 0 1px;
    border: solid 1px rgba(255, 255, 255, 0.37);
    transition: background-color 500ms;
    text-align: center;
}

.backing-table>.fixed>.matrix>div>.opr {
    width: 14px;
    background-color: rgba(3, 0, 185, 0.664);
}

.backing-table>.fixed>.matrix>div>.opr:hover {
    width: 14px;
    background-color: rgba(170, 169, 238, 0.664);
}

.backing-table>.fixed>.matrix>div>.channel {
    width: calc(100% - 34px);
}

.backing-table>.fixed>.matrix>div>.channel.blank {
    /* border: solid 1px rgba(255, 0, 0, 0.61); */
    background-color: rgba(136, 241, 255, 0.342);
}

.backing-table>.fixed>.matrix>div>div>span {
    display: inline-block;
    color: rgba(255, 255, 255, 0.37);
    font-size: 9px;
    margin-top: -1px;
    font-weight: 600;
    /* margin-left: 5px; */
}

.backing-table>.main>.matrix {
    min-width: calc(100% - 2px);
    width: initial;
    position: relative;
}

.backing-table>.main>.matrix>div {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
}

.backing-table>.main>.matrix>.cur {
    z-index: 9;
}

.backing-table>.main>.matrix>.back {
    z-index: 8;
    pointer-events: none;
}

.backing-table>.main>.matrix>div>.line {
    min-width: calc(100% - 2px);
    display: inline-block;
    white-space: nowrap;
    /* width: 100%; */
    height: 12px;
    margin: 1px 0 0 0;
    /* border: solid 1px rgba(255, 255, 255, 0.37); */
    /* background-color: rgba(111, 111, 240, 0.63); */
}

.backing-table>.main>.matrix>.cur>.line {
    /* background-color: rgba(111, 111, 240, 0.63); */
}

.backing-table>.main>.matrix>div>.line>div {
    display: inline-block;
    /* height: calc(100% - 2px); */
    height: 100%;
    /* border: solid 1px rgba(255, 0, 0, 0.226); */
    /* margin: 1px 0 1px 1px; */
    margin: 0 0 0 1px;
    transition: background-color 200ms, width 100ms;
}

.backing-table>.main>.matrix>.cur>.line>div>div {
    /* border: solid 1px rgba(255, 0, 0, 0.226); */
    background-color: rgba(0, 0, 0, 0.349);
}

.backing-table>.main>.matrix>.back>.line>div>div {
    /* border: solid 1px rgba(255, 0, 0, 0.226); */
    background-color: rgba(255, 255, 255, 0.055);
}

.backing-table>.main>.matrix>div>.line>div>div {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(0, 0, 0, 0.411); */
}

.backing-table>.main>.matrix>div>.line>div>div.div3:nth-child(1) {
    width: 33%;
    margin-right: 1px;
}

.backing-table>.main>.matrix>div>.line>div>div.div3:nth-child(2) {
    width: calc(33% - 1px);
    margin-right: 1px;
}

.backing-table>.main>.matrix>div>.line>div>div.div3:nth-child(3) {
    width: 33%;
}

.backing-table>.main>.matrix>.cur>.line>div>div.active {
    background: linear-gradient(rgb(94, 94, 0), yellow, rgb(94, 94, 0));
}

.backing-table>.main>.matrix>.cur>.line>div>div.overlap {
    background: linear-gradient(rgb(116, 116, 116), rgb(255, 255, 255), rgb(143, 143, 143));
    opacity: 0.5;
}

.backing-table>.main>.matrix>.back>.line>div>div.active, .backing-table>.main>.matrix>.back>.line>div>div.overlap {
    background: linear-gradient(rgb(94, 0, 0), rgb(255, 0, 0), rgb(94, 0, 0));
    opacity: 0.5;
}

.backing-table>.main>.matrix>div>.line>div>div:hover {
    background-color: rgba(209, 203, 203, 0.411);
}

.backing-table>div>.volume {
    height: 40px;
    background-color: rgba(255, 105, 180, 0.37);
    margin: 2px 2px 2px 2px;
}

.backing-table>.main>.volume {
    white-space: nowrap;
    width: initial;
}

.backing-table>.main>.volume>div {
    display: inline-block;
    height: calc(100% - 2px);
    /* height: 100%; */
    /* border: solid 1px rgba(255, 0, 0, 0.226); */
    background-color: rgba(110, 0, 0, 0.247);
    margin: 1px 0 1px 1px;
    /* margin: 0 0 0 1px; */
    transition: background-color 200ms, width 100ms;
    text-align: center;
    position: relative;
}

.backing-table>.main>.volume>div.active {
    background-color: rgba(255, 255, 255, 0.274);
}

.backing-table>.main>.volume>div>div {
    display: inline-block;
    position: absolute;
    /* height: 50%; */
    width: 12px;
    bottom: 0;
    left: calc(50% - 6px);
    background: linear-gradient(to right, rgb(8, 76, 97), rgb(0, 225, 255), rgb(8, 76, 97));
    /* margin: 1px 0 1px 1px; */
    transition: height 100ms;
}

/* ペダル */

.backing-table>.fixed>.pedal {
    height: 40px;
    background-color: rgba(0, 0, 0, 0.445);
    margin: 2px 0 2px 0;
}

.backing-table>.fixed>.pedal>button {
    display: inline-block;
    margin: 1px 0 0 2px;
    width: calc(100% - 4px);
    height: 20px;
    background-color: rgba(255, 255, 255, 0.233);
    border: solid 1px rgba(255, 255, 255, 0.658);
    border-radius: 2px;
    text-align: center;
}

.backing-table>.fixed>.pedal>button:hover {
    background-color: rgba(185, 229, 255, 0.397);
}

.backing-table>.fixed>.pedal>button.active {
    background-color: rgba(48, 244, 251, 0.562);
}

.backing-table>.fixed>.pedal>button.active:hover {
    background-color: rgba(255, 255, 255, 0.445);
}

.backing-table>.fixed>.pedal>button>span {
    display: inline-block;
    font-size: 12px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.658);
    margin-top: -1px;
}

#backing-palette>.operation {
    background-color: rgb(0, 11, 114);
    height: 30px;
    /* margin-top: 10px; */
    text-align: right;
}

#backing-palette>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 2px 5px 0 0;
    border: solid 1px rgba(255, 255, 255, 0.651);
    border-radius: 3px;
    text-align: center;
}

#backing-palette>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}

#backing-palette>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}

/*スクロールバー全体*/

#backing-palette>*>::-webkit-scrollbar {
    width: 5px;
    height: 8px;
}

/*スクロールバーの軌道*/

#backing-palette>*>::-webkit-scrollbar-track {
    border-radius: 5px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, .1);
}

/*スクロールバーの動く部分*/

#backing-palette>*>::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, .3);
}