#user-plate {
    display: inline-block;
    position: absolute;
    top: 0;
    right: 0;
    width: 400px;
    height: 40px;
    /* background-color: rgba(251, 49, 49, 0.24); */
}

#user-plate>.user-name {
    margin: 5px 0 0 0;
    width: 250px;
    height: 30px;
    border-radius: 14px;
    background-color: rgba(255, 255, 255, 0.164);
}

#user-plate>.user-name {
    display: inline-block;
    vertical-align: top;
}

#user-plate>.user-name>span {
    display: inline-block;
    margin: 2px 0 0 15px;
    color: rgba(255, 255, 255, 0.801);
    font-size: 22px;
    font-weight: 600;
}

#user-plate>.logout {
    display: inline-block;
    margin: 5px 0 0 10px;
    width: 110px;
    height: 30px;
    border-radius: 2px;
    background-color: rgba(149, 149, 149, 0.685);
    text-align: center;
}

#user-plate>.logout:hover {
    background-color: rgba(207, 207, 207, 0.685);
}

#user-plate>.logout>span {
    display: inline-block;
    margin: 3px 0 0 0;
    color: rgba(255, 255, 255, 0.801);
    font-size: 20px;
    font-weight: 600;
}