@import url('https://fonts.googleapis.com/css?family=Noto+Serif+JP');

#logo {
    display: inline-block;
    /* position: absolute; */
    width: 100%;
    height: 45px;
    /* background-color: rgba(0, 0, 0, 0.24); */
    background: linear-gradient( rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.463));
    /* top: 20px;
    left: 20px; */
}

#logo>span {
    display: inline-block;
    margin: 5px 0 0 10px ;
    font-size: 28px;
    font-weight: 600;
    font-family: 'Noto Serif JP', sans-serif;
    /* font-style: italic; */
    color: rgba(255, 255, 255, 0.637);
}

#login-frame {
    display: inline-block;
    position: absolute;
    width: 600px;
    height: 310px;
    background-color: rgba(255, 255, 255, 0.884);
    border-radius: 3px;
    /* border: solid 1px rgb(214, 214, 214); */
    /* margin-top: calc(50% - 200px);
    margin-left: calc(50% - 250px); */
    top: 50%;
    left: 50%;
    transform: translateY(-50%) translateX(-50%);
    box-shadow: 5px  5px 30px 30px #00000079;
    transition: opacity 300ms;
}

#login-frame>*>button {
    font-size: 20px;
    font-weight: 600;
    color: rgb(255, 255, 255);
    background-color: rgb(194, 214, 255);
    min-width: 100px;
    padding: 0 10px 0 10px;
    height: 35px;
    border-radius: 3px;
    /* border: solid 1px white; */
    text-align: center;
}
#login-frame>*>button:hover {
    background-color: rgb(158, 189, 250);
    box-shadow: 0 0 5px 5px #79efff1f;
}
#login-frame>*>button.active {
    background-color: rgba(220, 20, 60, 0.781);
    box-shadow: 0 0 5px 5px #ff798b28;
    /* animation: login-frame-bnt-flash 1s linear infinite; */
}

@keyframes login-frame-bnt-flash {
    0%, 100% {
        background-color: rgba(253, 80, 114, 0.781);
    }
    50% {
        background-color: rgba(182, 0, 36, 0.781);
    }
}

#login-frame>.guide {
    display: inline-block;
    width: 100%;
    height: 50px;
    /* background-color: rgba(238, 130, 238, 0.034); */
    text-align: center;
}
#login-frame>.guide>span {
    margin: 10px 0 0 0 ;
    display: inline-block;
    font-size: 24px;
    color: black;
}


#login-frame>.type {
    display: inline-block;
    width: 100%;
    height: 50px;
    /* background-color: rgba(0, 0, 255, 0.034); */
    text-align: center;
}

#login-frame>.type>button {
    margin: 7px 5px 0 5px;
}

#login-frame>.body {
    display: inline-block;
    width: 100%;
    height: 128px;
    background-color: rgba(226, 43, 43, 0.021);
}

#login-frame>.body>.form {
    display: inline-block;
    position: relative;
    margin-top: 2px;
    width: 100%;
    height: 40px;
    background-color: rgba(0, 0, 139, 0.027);
}

#login-frame>.body>.form>span {
    display: inline-block;
    margin: 10px 0 0 5px;
    font-size: 16px;
    color: black;
}
#login-frame>.body>.form>input {
    display: inline-block;
    position: absolute;
    left: 180px;
    top: 5px;
    width: 320px;
    height: 30px;
    background-color: rgb(255, 255, 255);
    border-radius: 2px;
    color: black;
    font-size: 16px;
    padding-left: 5px;
}
#login-frame>.body>.form>input.ban {
    background-color: rgba(255, 147, 147, 0.63);
}
#login-frame>.body>.form>input.range {
    background-color: rgba(104, 164, 255, 0.445);
}
#login-frame>.body>.form>input.required {
    background-color: rgba(243, 227, 0, 0.658);
}

#login-frame>.message {
    display: inline-block;
    width: 100%;
    height: 30px;
    background-color: rgba(34, 0, 0, 0.021);
}
#login-frame>.message>span {
    display: inline-block;
    margin: 8px 0 0 5px;
    font-size: 16px;
    color: rgb(33, 219, 0);
}
#login-frame>.message>.error {
    color: rgb(219, 0, 0);
}

#login-frame>.opr {
    display: inline-block;
    width: 100%;
    height: 50px;
    /* background-color: rgba(43, 226, 186, 0.075); */
    text-align: center;
}

#login-frame>.opr>button {
    display: inline-block;
    background-color: rgb(0, 177, 162);
    margin-top: 8px;
    width: calc(100% - 50px);
}
#login-frame>.opr>button:hover {
    background-color: rgb(86, 214, 161);
    box-shadow: 5px  5px 10px 10px #8dff793b;
}