
/* 選択中要素詳細 */
#current-detail {
    display: inline-block;
    width: 220px;
    height: 100%;
    background-color: rgb(255, 248, 226);
    outline: none;
    overflow: hidden;
}

#current-detail>div {
    display: inline-block;
    width: 100%;
    height: 100%;
}
#current-detail>.section {
    /* background-color: rgba(255, 0, 13, 0.089); */
    background-color: rgba(209, 209, 209, 0.158);
}
#current-detail>.chord {
    /* background-color: rgba(0, 0, 255, 0.158); */
    background-color: rgba(209, 209, 209, 0.158);
}

#current-detail>.chord>div {
    margin: 10px 0 0 0;
    display: inline-block;
    width: 100%;
}

#current-detail>.chord>div>.title {
    display: inline-block;
    width: calc(100% - 20px);
    margin: 0 0 0 10px;
    height: 25px;
    /* background-color: rgba(137, 43, 226, 0.315); */
}
#current-detail>.chord>div>.title>span {
    display: inline-block;
    margin: 0 0 0 0;
    font-size: 20px;
    font-weight: 600;
    color: rgba(211, 0, 0, 0.596);
}

.current-state {
    display: inline-block;
    margin: 0 5px 5px 5px;
    width: calc(100% - 10px);
    /* height: 227px; */
    border: solid 1px rgba(28, 0, 104, 0.158);
    background-color: rgba(0, 0, 0, 0.048);
    border-radius: 2px;
    text-align: center;
}

.current-state>.record {
    display: inline-block;
    width: calc(100% - 6px);
    height: 30px;
    margin: 1px 2px;
    /* background-color: rgba(0, 0, 0, 0.123); */
    text-align: left;
    border-bottom: solid 1px rgba(0, 0, 0, 0.089);
}
.current-state>.record>div {
    display: inline-block;
    margin: 2px 0 0 3px;
    height: calc(100% - 4px);
    /* background-color: rgba(255, 255, 255, 0.247); */
    vertical-align: top;
    /* text-align: center; */
}
.current-state>.record>div>span {
    display: inline-block;
    margin: 2px 5px 0 3px;
    font-size: 18px;
    font-weight: 600;
}

.current-state>.record>.title {
    width: 95px;
}
.current-state>.record>.title>span {
    color: rgb(65, 0, 0);
}
.current-state>.record>.value {
    width: 98px;
}
.current-state>.record>.value>span {
    color: rgb(255, 255, 255);
}

.symbol-struct {
    display: inline-block;
    margin: 0 5px 5px 5px;
    width: calc(100% - 10px);
    /* height: 227px; */
    border: solid 1px rgba(0, 0, 0, 0.158);
    border-radius: 2px;
    text-align: center;
}

.symbol-struct>.record {
    display: inline-block;
    width: calc(100% - 6px);
    height: 30px;
    margin: 1px 2px;
    background-color: rgba(0, 0, 0, 0.068);
    text-align: left;
}

.symbol-struct>.record>div {
    display: inline-block;
    margin: 2px 0 0 3px;
    height: calc(100% - 4px);
    background-color: rgba(255, 255, 255, 0.247);
    vertical-align: top;
    /* text-align: center; */
}
.symbol-struct>.record>div>span {
    display: inline-block;
    margin: 2px 5px 0 3px;
    color: rgba(0, 0, 0, 0.582);
    font-size: 18px;
    font-weight: 600;
}

.symbol-struct>.record>.interval {
    width: 35px;
}
.symbol-struct>.record>.struct {
    width: 115px;
}
.symbol-struct>.record>.sound {
    width: 40px;
}