.visible {
    display: none !important;
}

.box {
    display: inline-block;
    /* background-color: cornsilk; */
    transition: width 0.5s;
}

.box>div {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 5px;
    margin-left: 5px;
}

.box>.info-base {
    /* background-color: rgba(255, 67, 139, 0.075); */
}

.box>.info-base>.header {
    display: inline-block;
    width: 100%;
    height: 28px;
    /* background-color: rgba(255, 0, 0, 0.02); */
    position: relative;
}

.box>.info-base>.header>div {
    display: inline-block;
    position: absolute;
    bottom: 2px;
}

.box>.info-base>.header>.chord-no {
    left: 2px;
    width: 20px;
    height: 14px;
    /* background-color: rgba(241, 255, 112, 0.582); */
}

.box>.info-base>.header>.chord-no>span {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.39);
}
.box>.info-base>.header>.scale {
    left: 25px;
    width: 40px;
    height: 14px;
    /* border-bottom: solid 1px rgb(179, 0, 0); */
    /* background-color: rgba(255, 112, 112, 0.377); */
}
.box>.info-base>.header>.scale>span {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
}
.box>.info-base>.header>.scale.maj>span {
    color: rgba(173, 0, 0, 0.63);
}
.box>.info-base>.header>.scale.min>span {
    color: rgb(0, 0, 173, 0.63);
}

.box>.info-base>.header>.del {
    width: 24px;
    height: 24px;
    right: 2px;
    border: solid 1px rgba(29, 0, 160, 0.39);
    background-color: rgba(255, 255, 255, 0.13);
    border-radius: 2px;
    opacity: 0;
    text-align: center;
    box-shadow: 2px 2px 10px 2px #00000017;
}

.box>.info-base:hover>.header>.del {
    opacity: 1;
}

.box>.info-base:hover>.header>.del:hover {
    background-color: rgba(255, 0, 0, 0.438);
}

.box>.info-base>.header>.del>span {
    display: inline-block;
    font-size: 18px;
    font-weight: 600;
    color: rgba(12, 0, 184, 0.39);
}

.box>.addition {
    width: 50px;
    height: 50px;
    margin: 75px 0 0 5px;
    background-color: rgba(255, 255, 255, 0.068);
    border: solid 2px rgba(0, 0, 0, 0.11);
    border-radius: 2px;
    text-align: center;
    box-shadow: 2px 2px 10px 4px #0000002a;
}

.box>.addition>span {
    display: inline-block;
    line-height: 32px;
    font-size: 60px;
    /* font-weight: 600; */
    color: rgba(0, 0, 0, 0.308);
}

.box>.addition:hover {
    background-color: rgba(200, 255, 0, 0.103);
}

.note-element {
    display: block;
    text-align: center;
    height: 148px;
    transition: background-color 500ms;
    border-radius: 4px;
    /* box-shadow: 2px  2px 10px 2px #00000017; */
}

/* 初期値アイテム */

.note-element.detail-init {
    /* margin: 50px 0 0 5px; */
    width: 180px;
    padding: 10px;
    border: solid 2px rgba(0, 0, 0, 0.068);
    background-color: rgba(0, 0, 0, 0.02);
    border-radius: 3px;
    /* border-left: solid 2px rgba(41, 0, 95, 0.459);
    border-right: solid 2px rgba(41, 0, 95, 0.459); */
}

.note-element.detail-init>div:nth-child(1) {
    display: block;
    margin-top: 16px;
}

.note-element.detail-init>div:nth-child(1)>div {
    margin-top: 3px;
    display: block;
    position: relative;
    height: 20px;
    /* text-align: left; */
}

.note-element.detail-init>div:nth-child(1)>div>span {
    display: inline-block;
    position: absolute;
    top: 3px;
    font-size: 15px;
    font-weight: 600;
    /* background-color: rgba(238, 238, 238, 0.548); */
}

/* 初期化メニュータイトル */

.note-element.detail-init>div:nth-child(1)>div>span:nth-child(1) {
    left: 4px;
    color: rgba(0, 21, 61, 0.212);
}

/* 初期化メニュー値 */

.note-element.detail-init>div:nth-child(1)>div>span:nth-child(2) {
    left: 74px;
    color: rgba(0, 55, 156, 0.726);
}

.note-element.detail-chord {
    opacity: 0.4;
    min-width: 100px;
    padding: 5px 10px;
}

.note-element.selectable.current {
    opacity: 1;
    background-color: rgba(255, 255, 255, 0.911);
    border: solid 1px rgba(0, 47, 255, 0.13);
    box-shadow: 2px 2px 10px 4px #0000002a;
}

.note-element.detail-chord>div {
    display: block;
    margin-top: 2px;
    background-color: rgba(0, 136, 255, 0.041);
}

/* ディグリー */

.note-element.detail-chord>.degree>span {
    font-size: 20px;
    font-weight: 600;
    color: rgba(204, 0, 61, 0.664);
}

.note-element.detail-chord>.degree.diatonic>span {
    color: rgba(0, 21, 61, 0.664);
}

.note-element.detail-chord>.degree.blank>span {
    color: rgba(0, 21, 61, 0.144);
}

/* 実コード */

.note-element.detail-chord>.chord>span {
    font-size: 15px;
    font-weight: 600;
    color: rgba(194, 62, 124, 0.664);
}

/* 持続時間チップ（4分の1） */

.note-element.detail-chord>.sustain>span {
    display: inline-block;
    width: 10px;
    height: 20px;
    background: rgba(49, 49, 49, 0.356);
    margin: 2px 1px;
}

.note-element.detail-chord>.progress-sustain {
    position: relative;
}

.note-element.detail-chord>.progress-sustain>span {
    /* position: absolute; */
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    width: 50%;
}

.note-element.detail-chord>.progress-sustain>.bar {
    text-align: left;
    color: red;
}

.note-element.detail-chord>.progress-sustain>.beat {
    text-align: right;
    color: blue;
}

.note-element.detail-chord>.progress-time {
    position: relative;
    text-align: left;
}

.note-element.detail-chord>.progress-time>span {
    display: inline-block;
    font-size: 16px;
    font-weight: 600;
    color: rgba(0, 32, 24, 0.342);
}

.note-element.detail-chord>.backing-script {
    position: relative;
    text-align: left;
    background-color: rgba(0, 0, 0, 0.473);
    border-radius: 2px;
    height: 18px;
}

.note-element.detail-chord>.backing-script.none {
    background-color: rgba(0, 0, 0, 0.158);
}

.note-element.detail-chord>.backing-script>span {
    display: inline-block;
    font-size: 15px;
    font-weight: 600;
    margin: 0 0 0 2px;
    color: rgba(255, 255, 255, 0.877);
}

/* 変化アイテム */
.note-element.detail-change {
    /* margin: 50px 0 0 5px; */
    width: 120px;
    height: 130px;
    padding: 10px;
    border: solid 2px rgba(0, 0, 0, 0.068);
    background-color: rgba(0, 0, 0, 0.02);
    border-radius: 10px 10px 80px 80px;
    /* border-left: solid 2px rgba(41, 0, 95, 0.459);
    border-right: solid 2px rgba(41, 0, 95, 0.459); */
}

.note-element.detail-change>.change-type {
    margin-top: 3px;
    display: block;
    position: relative;
    height: 20px;
    text-align: center;
    /* background-color: rgba(255, 0, 0, 0.11); */
}

.note-element.detail-change>.change-type>span {
    /* margin-left: 4px; */
    font-size: 18px;
    font-weight: 600;
    color: rgba(0, 55, 156, 0.726);
}

.note-element.detail-change>.value {
    margin-top: 3px;
    display: block;
    position: relative;
    height: 20px;
    text-align: center;
    /* background-color: rgba(255, 0, 0, 0.11); */
}

.note-element.detail-change>.value>span {
    /* margin-left: 4px; */
    font-size: 13px;
    font-weight: 600;
}
.note-element.detail-change>.value>span:nth-child(1) {
    color: rgba(156, 0, 0, 0.726);
}
.note-element.detail-change>.value>span:nth-child(2) {
    color: rgba(43, 43, 43, 0.726);
}

.note-element.detail-change>.result {
    margin-top: 3px;
    display: block;
    position: relative;
    height: 20px;
    text-align: center;
    /* background-color: rgba(255, 0, 0, 0.11); */
}
.note-element.detail-change>.result>span {
    /* margin-left: 4px; */
    font-size: 14px;
    font-weight: 600;
    color: rgba(255, 0, 0, 0.726);
}


/* 固定 */
.note-element.detail-fixed {
    width: 100px;
    border: solid 1px rgba(0, 0, 0, 0.082);
    background-color: rgba(24, 0, 161, 0.027);
    border-radius: 30px 3px 3px 3px;
}

.note-element.detail-fixed>span {
    display: inline-block;
    margin-top: 40px;
    font-size: 22px;
    color: rgba(58, 33, 39, 0.445);
    font-weight: 600;
    line-height: 60px;
}

.note-element.detail-section {
    min-width: 80px;
    height: 150px;
}

.note-element.detail-section>.name {
    height: 30px;
    text-align: center;
    /* background-color: rgba(100, 148, 237, 0.13); */
}

.note-element.detail-section>.name>span {
    display: inline-block;
    margin-top: 10px;
    font-size: 16px;
    color: black;
}

.note-element.detail-section.current>.name>span {
    color: red;
    font-weight: 600;
}

.note-element.detail-section>.border {
    display: inline-block;
    height: calc(100% - 40px);
    margin-left: 14;
    width: 4px;
    background-color: rgba(185, 1, 1, 0.479);
}

.note-element.detail-section.current>.border {
    width: 6px;
    background-color: rgba(226, 26, 26, 0.76);
}

/* .note-element.current {
    opacity: 1;
    background-color: rgba(35, 68, 255, 0.048);
} */

.box>.info-base:hover>.note-element.selectable {
    opacity: 1;
    border: solid 1px rgba(0, 0, 0, 0.2);
    background-color: rgba(255, 72, 255, 0.075);
}

.box>.info-base>.note-element.selectable.active {
    background-color: rgba(0, 255, 0, 0.121);
    border: solid 3px rgba(0, 0, 0, 0.116);
    box-shadow: 2px 2px 15px 4px #000b3a48;
    z-index: 10;
}

.box>.info-base>.note-element.selectable.copy-tgt {
    opacity: 1;
    border: solid 1px rgba(255, 0, 0, 0.733);
    background-color: rgba(255, 255, 255, 0.726);
}

.box>.info-base>.note-element.selectable.copy-tgt:hover {
    opacity: 1;
    border: solid 1px rgba(255, 0, 0, 0.733);
    background-color: rgba(255, 224, 236, 0.836);
}

.box>.info-base>.note-element.selectable.copy-selected {
    opacity: 1;
    border: solid 1px rgba(255, 0, 0, 0.733);
    background-color: rgba(255, 96, 96, 0.712);
}

.box>.info-base>.note-element.selectable.copy-no-tgt {
    opacity: 0.2;
    pointer-events: none;
    /* background-color: rgba(255, 0, 0, 0.164); */
}

.box>.info-base>.note-element.selectable.criteria {
    animation: flash 1s linear infinite;
    border: solid 3px rgba(0, 0, 0, 0.116);
}

@keyframes flash {
    0%, 100% {
        background-color: rgba(0, 89, 255, 0.295);
    }
    50% {
        background-color: rgba(35, 68, 255, 0.048);
    }
}