
#init-editor {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 394px;
    /* height: 400px; */
    left: 100px;
    top: 100px
}

#init-editor>.items {
    display: inline-block;
    width: calc(100% - 4px);
    background-color: rgba(0, 255, 255, 0.13);
    margin: 0 0 0 2px;
}
#init-editor>.items>.item{
    display: inline-block;
    width: calc(100% - 4px);
    background-color: rgba(0, 0, 0, 0.13);
    margin: 1px 0 1px 2px;
    height: 36px;
}

#init-editor>.items>.item>div{
    display: inline-block;
    height: calc(100% - 4px);
    vertical-align: top;
}
#init-editor>.items>.item>.label{
    width: 90px;
    margin: 2px 0 0 2px;
    background-color: rgba(127, 255, 212, 0.075);
}
#init-editor>.items>.item>.label>span{
    display: inline-block;
    margin: 5px 0 0 4px;
    /* background-color: rgba(0, 0, 0, 0.205); */
    font-size: 18px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.815);
}
#init-editor>.items>.item>.value{
    width: calc(100% - 96px);
    margin: 2px 0 0 2px;
    background-color: rgba(255, 182, 182, 0.171);
}
#init-editor>.items>.item>.value>*{
    display: inline-block;
    vertical-align: top;
}
#init-editor>.items>.item>.value>input:nth-child(1){
    margin: 5px 0 0 10px;
    padding: 0 0 0 2px;
    /* background-color: blue; */
    width: 50px;
    color:  rgba(255, 255, 255, 0.747);
    background-color:  rgb(59, 59, 59);
    border-radius: 2px;
    border: solid 1px rgb(221, 221, 221);
    font-size: 18px;
    font-weight: 600;
    box-sizing: border-box;
}
#init-editor>.items>.item>.value>input:nth-child(2){
    margin: 10px 0 0 8px;
    height: calc(100% - 20px);
    width: calc(100% - 75px);
    background-color: rgba(0, 0, 0, 0.205);
    border: solid 1px rgba(0, 0, 0, 0.445);
    border-radius: 10px;
}


#init-editor>.items>.item>.value>select {
    width: calc(100% - 20px);
    height: calc(100% - 6px);
    margin: 3px 0 0 10px;
    font-size: 20px;
    font-weight: 600;
    border: solid 2px rgba(255, 255, 255, 0.589);
    border-radius: 3px;
    color: rgba(255, 255, 255, 0.781);
}

#init-editor>.items>.item>.value>select>option {
    background-color: black;
}


#init-editor>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 34px;
    text-align: right;
}
#init-editor>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 4px 5px 0 0;
    border: solid 1px white;
    text-align: center;
}
#init-editor>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}
#init-editor>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}