#scorecont-notetab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 127, 202, 0.664); */
    text-align: left;
}

#scorecont-notetab-root>div {
    display: inline-block;
    height: 100%;
    vertical-align: top;
}

/* 左側のコンポーネント */

#scorecont-notetab-root>.left-cont {
    display: inline-block;
    width: calc(100% - 223px);
}

/* スコアノートエリア */

#score-note {
    display: block;
    /* position: relative; */
    height: calc(100% - 150px);
    background-color: #eee;
    margin-bottom: 3px;
    overflow: hidden;
}

/* オペレーション */

#operation-header {
    /* display: inline-block; */
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    width: calc(100% - 10px);
    height: 40px;
    background-color: rgb(255, 255, 241);
    background: linear-gradient(rgb(236, 236, 236), rgb(255, 255, 241));
    /* background-color: rgba(30, 30, 165, 0.295); */
    overflow: hidden;
    text-align: left;
}
#operation-header.mode-selector {
    background-color: rgb(205, 223, 255);
    background: linear-gradient(rgb(236, 236, 236), rgb(205, 223, 255));
}

#operation-header>* {
    display: inline-block;
    vertical-align: top;
}

#operation-header>button {
    margin: 8px 3px;
    min-width: 80px;
    height: 25px;
    /* border: solid 1px rgba(139, 2, 2, 0.315); */
    background-color: rgb(187, 179, 161);
    border-radius: 2px;
    text-align: center;
}

#operation-header>button:hover {
    background-color: rgb(216, 206, 187);
}

#operation-header>button>span {
    display: inline-block;
    margin: 1px 15px 0 15px;
    color: rgba(255, 255, 255, 0.938);
    font-size: 14px;
    font-weight: 600;
}

#operation-header>.mode {
    margin: 3px 0 0 30px;
    width: 450px;
    height: 34px;
    /* background-color: khaki; */
}

#operation-header>.mode>* {
    display: inline-block;
    vertical-align: top;
}

#operation-header>.mode>.label {
    /* background-color: lawngreen; */
    width: 50px;
    height: calc(100% - 6px);
    margin: 3px 0 0 0;
}

#operation-header>.mode>.label>span {
    display: inline-block;
    font-size: 14px;
    font-weight: 600;
    color: rgba(136, 0, 0, 0.623);
    margin: 6px 0 0 0;
}

#operation-header>.mode>.border {
    border: solid 2px rgba(122, 0, 0, 0.534);
    height: calc(100% - 2px);
    margin: 1px 0 0 0;
    padding: 0 20px;
    border-radius: 2px;
}

#operation-header>.mode>.border>button {
    margin: 2px 5px;
    min-width: 80px;
    height: 25px;
    border: solid 1px rgba(139, 2, 2, 0.315);
    border-radius: 1px;
    text-align: center;
}

/* #operation-header>.mode>.border>button:hover {
    background-color: rgba(255, 255, 114, 0.185);
} */
#operation-header>.mode>.border>button.active {
    background-color: rgba(255, 182, 45, 0.267);
    box-shadow: 2px  2px 10px 4px #0000002a;
}

#operation-header>.mode>.border>button>span {
    display: inline-block;
    margin: 1px 15px 0 15px;
    color: rgba(139, 2, 2, 0.315);
    font-size: 12px;
    font-weight: 600;
}
#section-list {
    display: inline-block;
    margin-left: 5px;
    width: 90px;
    height: calc(100% - 50px);
    /* background-color: rgb(0, 255, 64); */
    background-color: rgb(255, 255, 241);
    vertical-align: top;
    text-align: center;
    border-right: solid 1px rgba(0, 42, 179, 0.849);
}
#section-list.mode-selector {
    background-color: rgb(205, 223, 255);
}

#section-list>div {
    display: inline-block;
    margin: 2px 1px;
    /* background: rgb(243, 232, 214); */
    /* border-radius: 3px; */
    border: solid 1px rgba(34, 34, 68, 0.267);
    width: 80px;
    height: 30px;
}

#section-list>div.active {
    /* background: rgba(248, 244, 8, 0.233); */
    background: rgba(248, 144, 8, 0.233);
    box-shadow: 2px  2px 10px 4px #0000002a;
    overflow-x: hidden;
}

#section-list>div>span {
    display: inline-block;
    font-size: 14px;
    color: rgba(34, 34, 68, 0.562);
    margin-top: 7px;
    /* font-weight: 600; */
}

#score-list-root {
    display: inline-block;
    margin-bottom: 5px;
    margin-right: 5px;
    width: calc(100% - 10px - 90px);
    height: calc(100% - 50px);
    /* height: 400px; */
    background-color: rgb(255, 255, 241);
    overflow-y: auto;
    text-align: left;
    outline: none;
}
#score-list-root.mode-selector {
    background-color: rgb(220, 233, 255);
}

#score-note.preview>div {
    background-color: rgb(235, 255, 241);
}

/* 右側のコンポーネント */

#scorecont-notetab-root>.right-cont {
    margin-left: 3px;
}

#modal-blind {
    display: none;
    position: absolute;
    background-color: rgba(0, 11, 41, 0.685);
    left: 0;
    top: 0;
    z-index: 90;
    width: 100%;
    height: 100%;
    opacity: 0;
    /* transition: opacity, display 200ms; */
}

#modal-blind.active {
    display: block;
    opacity: 1;
}