#scorecont-listtab-root {
    display: inline-block;
    width: 100%;
    height: 100%;
    /* background-color: rgba(255, 127, 202, 0.664); */
    text-align: left;
}

#scorecont-listtab-root>div {
    display: inline-block;
    height: 100%;
    vertical-align: top;
    background-color: #e7e7e7;
    text-align: left;
}

/* 左側のコンポーネント */

#scorecont-listtab-root>.left-cont {
    width: calc(50% - 2px);
    margin-right: 2px;
}

#scorecont-listtab-root>.left-cont>.opr, #scorecont-listtab-root>.right-cont>.opr {
    display: inline-block;
    width: calc(100% - 6px);
    height: 35px;
    margin: 3px 0 0 3px;
    background-color: rgba(240, 248, 255, 0.507);
    text-align: left;
}

#scorecont-listtab-root>.left-cont>.opr>button, #scorecont-listtab-root>.right-cont>.opr>button {
    display: inline-block;
    background-color: rgb(179, 179, 179);
    /* padding: 0 20px; */
    width: 120px;
    height: 30px;
    margin: 2px 0 0 2px;
    border-radius: 3px;
    border: solid 1px rgb(153, 153, 153);
    text-align: center;
}

#scorecont-listtab-root>.left-cont>.opr>button:hover, #scorecont-listtab-root>.right-cont>.opr>button:hover {
    background-color: rgb(209, 207, 207);
}

#scorecont-listtab-root>.left-cont>.opr>button.active, #scorecont-listtab-root>.right-cont>.opr>button.active {
    background-color: rgb(167, 85, 85);
}

#scorecont-listtab-root>.left-cont>.opr>button>span, #scorecont-listtab-root>.right-cont>.opr>button>span {
    display: inline-block;
    color: white;
    font-weight: 600;
    font-size: 20px;
    margin-top: -4px;
}

#scorecont-listtab-root>.left-cont>.lib {
    display: inline-block;
    width: calc(100% - 6px);
    height: calc(100% - 44px);
    margin: 3px 0 0 3px;
    background-color: rgba(255, 255, 255, 0.582);
    text-align: left;
}

#scorecont-listtab-root>.left-cont>.lib>.message {
    display: inline-block;
    /* background-color: rgba(255, 43, 43, 0.103); */
    width: 100%;
    height: 30px;
}

#scorecont-listtab-root>.left-cont>.lib>.message>span {
    display: inline-block;
    color: rgb(255, 0, 0);
    /* font-weight: 600; */
    font-size: 20px;
    margin: 0 0 0 5px;
}

#scorecont-listtab-root>.left-cont>.lib>.list {
    display: inline-block;
    /* background-color: rgba(255, 43, 43, 0.103); */
    width: calc(100% - 6px);
    height: calc(100% - 6px);
    margin: 3px 0 0 3px;
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record {
    display: inline-block;
    background-color: rgba(0, 0, 0, 0.014);
    width: calc(100% - 6px);
    height: 30px;
    margin: 3px 0 0 3px;
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record:hover {
    background-color: rgba(9, 116, 255, 0.103);
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record.focus {
    background-color: rgba(242, 255, 60, 0.521);
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record>span {
    display: inline-block;
    color: rgba(0, 0, 0, 0.473);
    /* font-weight: 600; */
    font-size: 22px;
    /* font-weight: 600; */
    /* margin: 0 0 0 5px; */
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record>.title {
    color: rgb(19, 0, 124);
}

#scorecont-listtab-root>.left-cont>.lib>.list>.record>.mode {
    color: rgb(0, 93, 20);
}

/* 右側のコンポーネント */

#scorecont-listtab-root>.right-cont {
    width: calc(50% - 2px);
    margin-left: 2px;
}

#scorecont-listtab-root>.right-cont>.data {
    display: inline-block;
    width: calc(100% - 6px);
    height: calc(100% - 44px);
    margin: 3px 0 0 3px;
    background-color: rgba(255, 255, 255, 0.582);
    text-align: left;
}

#scorecont-listtab-root>.right-cont>.data>.info-record {
    display: inline-block;
    background-color: rgba(0, 0, 0, 0.014);
    width: calc(100% - 6px);
    height: 30px;
    margin: 3px 0 0 3px;
}

#scorecont-listtab-root>.right-cont>.data>.info-record>* {
    display: inline-block;
    /* background-color: aqua; */
    font-size: 18px;
    color: red;
    margin: 4px 0 0 3px;
    vertical-align: top;
}

#scorecont-listtab-root>.right-cont>.data>.info-record>input {
    background-color: white;
    color: black;
    border:  solid 1px rgb(192, 192, 192);
    margin-left: 10px;
    padding-left: 5px;
}

#scorecont-listtab-root>.right-cont>.data>.info-record>.value {
    color: black;
    margin-left: 20px;
}
#scorecont-listtab-root>.right-cont>.data>.info-record>select.value {
    color: black;
    margin-left: 20px;
    border: solid 1px red;
    padding: 0 20px;
    background-color: rgba(255, 251, 0, 0.233);
}
#scorecont-listtab-root>.right-cont>.data>.info-record>select.value>option {
    background-color: white;
}

#scorecont-listtab-root>.left-cont>button {
    display: inline-block;
    margin: 88px 5px;
    min-width: 280px;
    height: 125px;
    border: solid 3px rgba(139, 2, 2, 0.315);
    border-radius: 10px;
    text-align: center;
    background-color: rgba(184, 184, 184, 0.315);
    /* box-shadow: 5px  5px 30px 10px #0000001f; */
}

#scorecont-listtab-root>.left-cont>button:hover {
    background-color: rgba(236, 236, 236, 0.315);
    box-shadow: 5px 5px 30px 10px #00000059;
}

#scorecont-listtab-root>.left-cont>button>span {
    display: inline-block;
    margin: -8px 15px 0 15px;
    color: rgba(139, 2, 2, 0.315);
    font-size: 42px;
    font-weight: 600;
}