#total-palette {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 594px;
    /* height: 400px; */
    left: 100px;
    top: 100px
}

#selector-tabs {
    /* height: 200px; */
}

#total-palette>div {
    display: inline-block;
    width: calc(100% - 10px);
    margin: 5px 5px;
}

#total-palette>div.active {
    background-color: rgba(255, 38, 0, 0.116);
}

#total-palette>div>div>div>.chord-selector {
    background-color: rgba(0, 0, 0, 0.452);
    height: 130px;
}

#total-palette>div>div>div>.chord-selector>div {
    display: inline-block;
    width: 91px;
    height: 58px;
    margin: 2px;
    vertical-align: top;
}

#total-palette>div>div>div>.chord-selector>div>div {
    display: inline-block;
    text-align: center;
    width: 100%;
    height: 100%;
    background-color: rgba(50, 216, 238, 0.075);
    border: solid 1px rgba(255, 255, 255, 0.164);
}

#total-palette>div>div>div>.chord-selector>div>div:hover {
    background-color: rgba(255, 255, 255, 0.452);
}

#total-palette>div>div>div>.chord-selector>div>div.active {
    background-color: rgba(252, 227, 0, 0.623);
}

#total-palette>div>div>div>.chord-selector>div>div.denominator {
    background-color: rgba(252, 122, 0, 0.623);
}

#total-palette>div>div>div>.chord-selector>div>.half {
    height: 27px;
}

#total-palette>div>div>div>.chord-selector>div>.half:nth-child(1) {
    margin-bottom: 4px;
}

#total-palette>div>div>div>.chord-selector>div>div>span {
    display: inline-block;
    color: rgba(255, 255, 255, 0.37);
    font-size: 16px;
    font-weight: 600;
    margin-top: 18px;
}

#total-palette>div>div>div>.chord-selector>div>.half>span {
    margin-top: 3px;
}

#total-palette>div>div>div>.chord-selector>div>div>.degree {
    color: rgba(255, 255, 255, 0.932);
}

#total-palette>div>div>div>.chord-selector>div>div>.chord {
    color: rgba(255, 255, 255, 0.555);
}

#total-palette>div>div>div>.symbol-selector {
    background-color: rgba(0, 0, 0, 0.685);
    height: 180px;
    text-align: left;
}

#total-palette>div>div>div>.symbol-selector>div:hover {
    background-color: rgba(255, 255, 255, 0.452);
}

#total-palette>div>div>div>.symbol-selector>div.active {
    background-color: rgba(252, 227, 0, 0.623);
}

#total-palette>div>div>div>.symbol-selector>div {
    display: inline-block;
    height: 28px;
    width: 91px;
    margin: 2px;
    vertical-align: top;
    background-color: rgba(238, 130, 238, 0.295);
    border: solid 1px rgba(255, 255, 255, 0.164);
}

#total-palette>div>div>div>.symbol-selector>div>span {
    display: inline-block;
    margin: 3px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: yellow;
}

#total-palette>.beat-selector {
    /* background-color: rgba(255, 255, 255, 0.219); */
    margin: 0 0 0 6px;
    width: calc(100% - 12px);
    /* height: 60px; */
}

#total-palette>.beat-selector>div {
    display: inline-block;
    width: 67px;
    height: 28px;
    margin: 2px;
    vertical-align: top;
    background-color: rgba(255, 255, 255, 0.123);
}

#total-palette>.beat-selector>div:hover {
    background-color: rgba(255, 255, 255, 0.336);
}

#total-palette>.beat-selector>div.active {
    background-color: rgba(255, 255, 255, 0.815);
}

#total-palette>.beat-selector>div.active:hover {
    background-color: rgba(255, 255, 255, 0.589);
}

#total-palette>.piano-view {
    background-color: rgba(255, 255, 255, 0.219);
    margin: 0 0 0 6px;
    width: calc(100% - 12px);
    height: 70px;
}

#total-palette>.piano-view>canvas {
    display: inline-block;
    width: calc(100% - 4px);
    height: calc(100% - 4px);
    margin: 2px 0 0 2px;
}

#total-palette>.adjuster {
    background-color: rgba(255, 255, 255, 0.219);
    margin: 4px 0 0 6px;
    width: calc(100% - 12px);
    height: 20px;
}

#total-palette>.adjuster>div {
    display: inline-block;
    height: calc(100% - 4px);
    margin-top: 2px;
    vertical-align: top;
    background-color: rgb(64, 64, 136);
    width: 55px;
    text-align: center;
    border-radius: 2px;
    border: solid 1px rgb(219, 219, 219);
    margin-left: 2px;
    /* opacity: 0.6; */
}

#total-palette>.adjuster>div:nth-child(1) {
    margin-left: 4px;
}

#total-palette>.adjuster>div:nth-child(6) {
    margin-left: 4px;
}

#total-palette>.adjuster>div>span {
    display: inline-block;
    color: rgb(219, 219, 219);
    font-size: 14px;
    font-weight: 600;
    margin-top: -1px;
}

#total-palette>.adjuster>.head {
    background-color: rgba(116, 152, 173, 0.411);
}

#total-palette>.adjuster>.head:hover {
    background-color: rgba(116, 152, 173, 0.811);
    color: white;
}

#total-palette>.adjuster>.tail {
    background-color: rgb(173, 116, 124, 0.411);
}

#total-palette>.adjuster>.tail:hover {
    background-color: rgb(173, 116, 124, 0.811);
    color: white;
}

#total-palette>.adjuster>.head.active, #total-palette>.adjuster>.tail.active {
    background-color: rgba(162, 185, 62, 0.811);
}

#total-palette>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 30px;
    margin-top: 10px;
    text-align: right;
}

#total-palette>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 2px 5px 0 0;
    border: solid 1px white;
    text-align: center;
}

#total-palette>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}

#total-palette>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}