
#change-editor {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 394px;
    /* height: 400px; */
    left: 100px;
    top: 100px
}

#change-editor>.items {
    background-color: rgba(0, 68, 124, 0.644);
    /* height: 34px; */
    text-align: left;
}

#change-editor>.items>.record {
    display: inline-block;
    width: 100%;
    height: 32px;
    margin: 2px 0 0 0;
    background-color: rgba(255, 255, 255, 0.356);
}

#change-editor>.items>.record>*{
    vertical-align: top;
    height: 28px;
    margin: 2px 0 0 0;
    padding: 2px;
    box-sizing: border-box;
}


#change-editor>.items>.record>.label {
    display: inline-block;
    width: 45%;
    background-color: rgba(255, 255, 255, 0.356);
    font-size: 18px;
    color: rgb(255, 255, 255);
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
    font-weight: 600;
    padding-left: 2px;
    box-sizing: border-box;
}

#change-editor>.items>.record>.value {
    display: inline-block;
    width: 55%;
    background-color: rgba(212, 0, 0, 0.356);
}

#change-editor>.items>.record>.value>select {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.767);
    font-size: 18px;
    color: black;
    font-weight: 600;
    padding-left: 2px;
    box-sizing: border-box;
}
#change-editor>.items>.record>.value>select.error {
    background-color: rgba(255, 255, 0, 0.74);
}

#change-editor>.items>.record>.value>select>option {
    font-size: 18px;
    color: black;
    font-weight: 600;
    padding-left: 2px;
    box-sizing: border-box;
}

#change-editor>.items>.record>.value>input {
    display: inline-block;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.767);
    font-size: 18px;
    color: black;
    font-weight: 600;
    padding-left: 2px;
    box-sizing: border-box;
}

#change-editor>.items>.record>.value>input.error {
    background-color: rgba(255, 255, 0, 0.74);
}

#change-editor>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 34px;
    text-align: right;
}
#change-editor>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 4px 5px 0 0;
    border: solid 1px rgba(255, 255, 255, 0.651);
    border-radius: 3px;
    text-align: center;
}
#change-editor>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}
#change-editor>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}
