.tab-list {
    display: inline-block;
    height: 30px;
    /* width: 100%; */
    font-size: 0;
    /* background-color: royalblue; */
}

.tab-menu {
    display: inline-block;
    /* width: 100px; */
    height: 100%;
    background-color: #99a;
    color: #555;
    opacity: 0.7;
    border-radius: 3px 3px 0 0;
    user-select: none;
    margin-right: 5px;
}

.tab-menu:hover {
    background-color: rgb(177, 177, 190);
}

.tab-menu>span {
    display: inline-block;
    margin-top: 7px;
    padding: 0 5px;
    font-size: 16px;
    font-weight: 600;
}

.cont-list {
    display: block;
    background-color: rgba(153, 153, 170, 0.692);
    height: calc(100% - 30px);
    border-radius: 0 6px 6px 6px;
}

.tab-cont {
    display: inline-block;
    /* background-color: rgba(0, 0, 0, 0.1); */
    height: calc(100% - 10px);
    width: calc(100% - 10px);
    margin: 5px;
    text-align: center;
    vertical-align: top;
}

/* .tab-cont.active {
    display: inline-block !important;
} */

.tab-disable {
    opacity: 0.3;
    pointer-events: none ;
    user-select: none;
}
.disable {
    opacity: 0.3 !important;
    pointer-events: none !important;
    user-select: none !important;
}

.tab-menu.active {
    opacity: 1;
    color: #fff;
    background-color: rgb(177, 177, 190);
    padding: 0 20px;
    transition: padding 100ms;
    user-select: none;
    /* box-shadow: 0 10px 25px 0 rgba(60, 194, 235, 0.5); */
}

.hidden {
    display: none !important;
}