
#setting-editor {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 348px;
    height: 400px;
    left: 30px;
    top: 126px
}

#setting-editor>div {
    display: inline-block;
    height: 40px;
    width: 100%;
    margin-top: 2px;
}
#setting-editor>div:nth-child(1) {
    margin-top: 0;
}
#setting-editor>.sustain {
    background-color: rgba(255, 9, 9, 0.699);
    text-align: center;
}
#setting-editor>.sustain>div {
    display: inline-block;
    width: 30px;
    height: calc(100% - 6px);
    background-color: white;
    margin: 3px 1px 0 1px;
    opacity: 0.4;
}
#setting-editor>.sustain>div:hover {
    opacity: 0.6;
    background-color: yellow;
}
#setting-editor>.sustain>div.active {
    opacity: 1;
}
