
#section-editor {
    background-color: rgba(0, 0, 0, 0.911);
    /* border: solid 2px rgb(0, 255, 13); */
    border: solid 2px rgb(207, 207, 207);
    width: 394px;
    /* height: 400px; */
    left: 100px;
    top: 100px
}


#section-editor>.items {
    display: inline-block;
    width: calc(100% - 4px);
    background-color: rgb(0, 224, 224);
    margin: 0 0 0 2px;
}
#section-editor>.items>.item{
    display: inline-block;
    width: calc(100% - 4px);
    background-color: rgba(226, 0, 0, 0.863);
    margin: 1px 0 1px 2px;
    height: 36px;
}

#section-editor>.items>.item>div{
    display: inline-block;
    height: calc(100% - 4px);
    vertical-align: top;
    background-color: rgba(89, 31, 250, 0.877);
    margin-top: 2px;
}
#section-editor>.items>.item>.label{
    width: 90px;
    margin-left: 2px;
    background-color: rgba(127, 255, 212, 0.637);
}
#section-editor>.items>.item>.label>span{
    display: inline-block;
    margin: 5px 0 0 4px;
    /* background-color: rgba(0, 0, 0, 0.205); */
    font-size: 18px;
    font-weight: 600;
    color: rgba(255, 255, 255, 0.815);
}

#section-editor>.items>.item>.value>input {
    width: calc(100% - 20px);
    height: calc(100% - 6px);
    margin: 3px 0 0 10px;
    font-size: 20px;
    font-weight: 600;
    border: solid 2px rgba(255, 255, 255, 0.589);
    border-radius: 3px;
    color: rgba(255, 255, 255, 0.781);
}

#section-editor>.operation {
    background-color: rgba(146, 206, 255, 0.068);
    height: 34px;
    text-align: right;
}
#section-editor>.operation>div {
    display: inline-block;
    width: 100px;
    height: 26px;
    background-color: rgba(255, 255, 255, 0.219);
    margin: 4px 5px 0 0;
    border: solid 1px rgba(255, 255, 255, 0.651);
    border-radius: 3px;
    text-align: center;
}
#section-editor>.operation>div:hover {
    background-color: rgba(255, 255, 255, 0.534);
}
#section-editor>.operation>div>span {
    display: inline-block;
    margin: 2px 7px 0 7px;
    font-size: 16px;
    font-weight: 600;
    color: rgb(255, 255, 255);
}

